package presentation.feed

import cafe.adriel.voyager.core.model.StateScreenModel
import cafe.adriel.voyager.core.model.screenModelScope
import data.model.ProfileWithPhotos
import data.repository.ProfileRepository
import kotlinx.coroutines.launch

class FeedViewModel(
    private val profileRepository: ProfileRepository
) : StateScreenModel<FeedViewModel.State>(State.Init) {

    sealed class State {
        object Init : State()
        object Loading : State()
        object NoMoreProfiles : State()
        data class RateProfile(val profileWithPhotos: ProfileWithPhotos) : State()
        data class ProfileWithRate(
            val profileWithPhotos: ProfileWithPhotos,
            val targetRate: Double
        ) : State()
    }

    init {
        feedNext()
    }

    fun feedNext() {
        screenModelScope.launch {
            mutableState.value = State.Loading
            val nextProfile = profileRepository.feedNext()
            if (nextProfile != null) {
                mutableState.value = State.RateProfile(nextProfile)
            } else {
                mutableState.value = State.NoMoreProfiles
            }
        }
    }

    fun rateProfile(score: Int) {
        screenModelScope.launch {
            val currentState = state.value
            if (currentState is State.RateProfile) {
                val targetId = currentState.profileWithPhotos.profile.id
                val targetRate = profileRepository.rateProfile(targetId, score)
                mutableState.value =
                    State.ProfileWithRate(currentState.profileWithPhotos, targetRate)
            }
        }

    }
}