@OptIn(ExperimentalMaterial3Api::class, ExperimentalResourceApi::class)
@Composable
fun SetupDateView(viewModel: OnboardingViewModel, onBackPressed: () -> Unit) {
    val selectableDates = object : SelectableDates {
        override fun isSelectableDate(utcTimeMillis: Long): Boolean {
            val possibleDate = Clock.System.now()
                .minus(14, DateTimeUnit.YEAR, TimeZone.UTC)
                .toEpochMilliseconds()
            return utcTimeMillis <= possibleDate
        }
    }
    val dateState = rememberDatePickerState(
        initialDisplayMode = DisplayMode.Input,
        selectableDates = selectableDates
    )
    var selectedDate by remember { mutableStateOf<LocalDate?>(null) }

    Scaffold(
        topBar = {
            TopAppBar(
                title = {},
                navigationIcon = {
                    IconButton(
                        onClick = { onBackPressed() }
                    ) {
                        Icon(Icons.AutoMirrored.Filled.ArrowBack, contentDescription = null)
                    }
                },
                colors = TopAppBarDefaults.topAppBarColors(containerColor = Color.Transparent)
            )
        }
    ) { paddingValues ->
        Box(
            modifier = Modifier
                .fillMaxSize()
                .background(color = MaterialTheme.colorScheme.background)
        )
        {
            Image(
                modifier = Modifier.matchParentSize(),
                painter = painterResource(Res.drawable.background_blured),
                contentDescription = null,
                contentScale = ContentScale.FillBounds
            )
            Column(
                modifier = Modifier
                    .padding(paddingValues)
                    .padding(start = 16.dp, top = 74.dp, end = 16.dp, bottom = 52.dp),
                verticalArrangement = Arrangement.SpaceBetween
            ) {
                Box(
                    modifier = Modifier.weight(1f)
                ) {
                    Column {
                        Text(
                            text = stringResource(Res.string.onboarding_date_h1),
                            fontWeight = FontWeight.Bold,
                            fontSize = 34.sp,
                            color = accent
                        )
                        Spacer(modifier = Modifier.height(43.dp))
                        DatePicker(
                            modifier = Modifier
                                .fillMaxWidth(),
                            state = dateState,
                            title = null,
                            headline = null,
                            showModeToggle = false
                        )
                    }
                }
                Button(
                    modifier = Modifier.fillMaxWidth(),
                    enabled = dateState.selectedDateMillis != null,
                    onClick = {
                        viewModel.setupDate(dateState.selectedDateMillis!!)
                    },
                    colors = ButtonDefaults.buttonColors(
                        containerColor = accent,
                        contentColor = Color.Black
                    )
                ) {
                    Text(stringResource(Res.string.onboarding_button_next))
                }
            }
        }
    }
}
