package data.network

import com.project87.api.model.dto.RateProfileRequest
import com.project87.api.model.dto.RateProfileResponse
import data.model.CreateProfileRequest
import data.model.CreateProfileResponse
import data.model.EditProfileRequest
import data.model.EditProfileResponse
import data.model.GetFeedNextResponse
import data.model.GetMyProfilePageResponse
import data.model.GetMyProfileResponse
import data.model.GetProfileResponse
import data.model.ProfileIsCreatedResponse
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.plugins.DefaultRequest
import io.ktor.client.plugins.contentnegotiation.ContentNegotiation
import io.ktor.client.request.get
import io.ktor.client.request.header
import io.ktor.client.request.post
import io.ktor.client.request.put
import io.ktor.client.request.setBody
import io.ktor.http.ContentType
import io.ktor.http.HttpHeaders
import io.ktor.http.contentType
import io.ktor.serialization.kotlinx.json.json
import kotlinx.serialization.json.Json

class ProfileApi(
    private val supabaseClient: SupabaseClient,
//    private val baseUrl: String = "http://10.0.2.2:8091"
//    private val baseUrl: String = "https://project87-pomarkllc.koyeb.app"
    private val baseUrl: String = "http://176.124.192.177:8091"
) {
    private val client = HttpClient {
        install(ContentNegotiation) {
            json(Json {
                isLenient = true
                ignoreUnknownKeys = true
                useAlternativeNames = false
            })
        }
        install(DefaultRequest) {
            header(HttpHeaders.Authorization, "Bearer ${supabaseClient.getToken()}")
            url(baseUrl)
        }
    }

    suspend fun getProfile(id: String): GetProfileResponse {
        return client.get("/api/v1/profile/$id").body()
    }

    suspend fun getMyProfilePage(): GetMyProfilePageResponse {
        return client.get("/api/v1/profile/my/page").body()
    }

    suspend fun getMyProfile(): GetMyProfileResponse {
        return client.get("/api/v1/profile/my").body()
    }

    suspend fun createProfile(createProfileRequest: CreateProfileRequest): CreateProfileResponse {
        return client.post("/api/v1/profile") {
            contentType(ContentType.Application.Json)
            setBody(createProfileRequest)
        }.body()
    }

    suspend fun editProfile(editProfileRequest: EditProfileRequest): EditProfileResponse {
        return client.put("/api/v1/profile") {
            contentType(ContentType.Application.Json)
            setBody(editProfileRequest)
        }.body()
    }

    suspend fun profileIsCreated(): ProfileIsCreatedResponse {
        return client.get("/api/v1/profile/is_created").body()
    }

    suspend fun getFeedNext(): GetFeedNextResponse {
        return client.get("/api/v1/feed").body()
    }

    suspend fun rateProfile(targetProfileId: String, score: Int): RateProfileResponse {
        return client.post("/api/v1/feed/$targetProfileId/rate") {
            contentType(ContentType.Application.Json)
            setBody(RateProfileRequest(score))
        }.body()
    }
}