package presentation.profile

import cafe.adriel.voyager.core.model.StateScreenModel
import cafe.adriel.voyager.core.model.screenModelScope
import data.repository.ProfileRepository
import kotlinx.coroutines.launch

class SettingsViewModel(
    private val repository: ProfileRepository
) : StateScreenModel<SettingsViewModel.State>(State.Init) {

    sealed class State {
        object Init : State()

        object ShowSettings : State()

        object Loading : State()

        object Success : State()

        object SignOut : State()

        object SignOutSuccess : State()

    }

    init {
        mutableState.value = State.Loading
        mutableState.value = State.ShowSettings
    }

    fun saveSettings() {
        mutableState.value = State.Loading
        // save settings
        mutableState.value = State.Success
    }

    fun signOut() {
        screenModelScope.launch {
            mutableState.value = State.SignOut
            repository.signOut()
            mutableState.value = State.SignOutSuccess
        }
    }
}