class LoginScreen : Screen {

    @Composable
    override fun Content() {
        val repository = koinInject<ProfileRepository>()
        val viewModel = rememberScreenModel { LoginScreenViewModel(repository) }
        val state = viewModel.state.collectAsState()
        val navigator = LocalNavigator.currentOrThrow

        when (val currentState = state.value) {
            is LoginScreenViewModel.State.ProfileNotCreated -> navigator.push(OnboardingScreen())
            is LoginScreenViewModel.State.Authorized -> TabNavigation()
            is LoginScreenViewModel.State.Login -> {
                LoginFormView(viewModel, currentState.errorMessage ?: "")
            }

            else -> {
                LoadingView()
            }
        }
    }
}

@OptIn(ExperimentalResourceApi::class)
@Composable
private fun LoginFormView(viewModel: LoginScreenViewModel, errorMessage: String) {

    Box(
        modifier = Modifier
            .fillMaxSize()
            .background(color = MaterialTheme.colorScheme.background),
        contentAlignment = Alignment.Center
    ) {

        val navigator = LocalNavigator.currentOrThrow
        var email by remember {
            mutableStateOf("")
        }

        var password by remember {
            mutableStateOf("")
        }

        val enableLogin = email.isNotBlank() && password.isNotBlank()

        Image(
            modifier = Modifier.matchParentSize(),
            painter = painterResource(Res.drawable.background_blured),
            contentDescription = null,
            contentScale = ContentScale.FillBounds
        )

        Column(
            modifier = Modifier
                .padding(start = 16.dp, top = 83.dp, end = 16.dp, bottom = 52.dp),
            verticalArrangement = Arrangement.SpaceBetween,
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Image(
                modifier = Modifier
                    .size(width = 274.dp, height = 237.dp)
                    .weight(1f),
                painter = painterResource(Res.drawable.logo),
                contentDescription = null
            )
            Spacer(modifier = Modifier.height(40.dp))
            Box(
                modifier = Modifier.weight(1f)
            ) {
                Column(
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    Text(
                        text = errorMessage,
                        color = Color.Red
                    )
                    Spacer(modifier = Modifier.height(8.dp))
                    OutlinedTextField(
                        modifier = Modifier.fillMaxWidth(),
                        value = email,
                        singleLine = true,
                        onValueChange = { email = it },
                        label = { Text(stringResource(Res.string.email_field)) },
                        keyboardOptions = KeyboardOptions(keyboardType = KeyboardType.Email)
                    )
                    Spacer(modifier = Modifier.height(16.dp))
                    OutlinedTextField(
                        modifier = Modifier.fillMaxWidth(),
                        value = password,
                        singleLine = true,
                        onValueChange = { password = it },
                        label = { Text(stringResource(Res.string.password)) },
                        visualTransformation = PasswordVisualTransformation(),
                        keyboardOptions = KeyboardOptions(keyboardType = KeyboardType.Password)
                    )
                }
            }
            Button(
                modifier = Modifier.fillMaxWidth(),
                enabled = enableLogin,
                onClick = {
                    viewModel.signIn(email, password)
                },
                colors = ButtonDefaults.buttonColors(
                    containerColor = accent,
                    contentColor = Color.Black
                )
            ) {
                Text(stringResource(Res.string.login_button))
            }

            Spacer(modifier = Modifier.height(20.dp))

            Text(
                modifier = Modifier.clickable {
                    navigator.push(CreateAccountScreen())
                },
                text = buildAnnotatedString {
                    append(stringResource(Res.string.login_reg_text1))
                    withStyle(
                        SpanStyle(fontWeight = FontWeight.Bold)
                    ) {
                        append(stringResource(Res.string.login_reg_text2))
                    }
                },
                fontSize = 13.sp,
                color = primary100
            )
        }
    }
}
