@OptIn(ExperimentalResourceApi::class, ExperimentalMaterial3Api::class)
class CreateAccountScreen : Screen {

    @Composable
    override fun Content() {
        val repository = koinInject<ProfileRepository>()
        val viewModel = rememberScreenModel { CreateAccountViewModel(repository) }
        val state = viewModel.state.collectAsState()
        val navigator = LocalNavigator.currentOrThrow

        when (val currentState = state.value) {
            is CreateAccountViewModel.State.Init -> {}
            is CreateAccountViewModel.State.FillData -> {
                CreateAccountView(viewModel, navigator)
            }

            is CreateAccountViewModel.State.Loading -> {
                LoadingView("Wait for account creation")
            }

            is CreateAccountViewModel.State.Success -> {
                navigator.replace(OnboardingScreen())
            }
        }
    }

    @Composable
    private fun CreateAccountView(viewModel: CreateAccountViewModel, navigator: Navigator) {
        var email by remember {
            mutableStateOf("")
        }

        var password by remember {
            mutableStateOf("")
        }

        var passwordRe by remember {
            mutableStateOf("")
        }

        val enableCreate =
            (email.isNotBlank() && password.isNotBlank() && passwordRe.isNotBlank())
                    && (password == passwordRe)

        Scaffold(
            topBar = {
                TopAppBar(
                    title = {},
                    navigationIcon = {
                        IconButton(
                            onClick = { navigator.pop() }
                        ) {
                            Icon(Icons.AutoMirrored.Filled.ArrowBack, contentDescription = null)
                        }
                    },
                    colors = TopAppBarDefaults.topAppBarColors(containerColor = Color.Transparent)
                )
            }
        ) { paddingValues ->
            Box(
                modifier = Modifier
                    .fillMaxSize()
                    .background(color = MaterialTheme.colorScheme.background)
            )
            {
                Image(
                    modifier = Modifier.matchParentSize(),
                    painter = painterResource(Res.drawable.background_blured),
                    contentDescription = null,
                    contentScale = ContentScale.FillBounds
                )
                Column(
                    modifier = Modifier
                        .padding(paddingValues)
                        .padding(start = 16.dp, top = 40.dp, end = 16.dp, bottom = 52.dp),
                    verticalArrangement = Arrangement.SpaceBetween
                ) {
                    Box(
                        modifier = Modifier.weight(1f)
                    ) {
                        Column {
                            Text(
                                text = stringResource(Res.string.create_acc_h1),
                                fontWeight = FontWeight.Bold,
                                fontSize = 34.sp,
                                color = accent
                            )
                            Spacer(modifier = Modifier.height(8.dp))
                            Text(
                                text = stringResource(Res.string.create_acc_h2),
                                fontSize = 22.sp,
                                color = primary100
                            )
                            Spacer(modifier = Modifier.height(43.dp))
                            OutlinedTextField(
                                modifier = Modifier.fillMaxWidth(),
                                value = email,
                                onValueChange = { email = it },
                                singleLine = true,
                                label = { Text(stringResource(Res.string.email_field)) },
                                keyboardOptions = KeyboardOptions(keyboardType = KeyboardType.Email)
                            )
                            Spacer(modifier = Modifier.height(8.dp))
                            OutlinedTextField(
                                modifier = Modifier.fillMaxWidth(),
                                value = password,
                                onValueChange = { password = it },
                                singleLine = true,
                                label = { Text(stringResource(Res.string.password)) },
                                visualTransformation = PasswordVisualTransformation(),
                                keyboardOptions = KeyboardOptions(keyboardType = KeyboardType.Password)
                            )
                            Spacer(modifier = Modifier.height(8.dp))
                            OutlinedTextField(
                                modifier = Modifier.fillMaxWidth(),
                                value = passwordRe,
                                onValueChange = { passwordRe = it },
                                singleLine = true,
                                label = { Text(stringResource(Res.string.password_re)) },
                                visualTransformation = PasswordVisualTransformation(),
                                keyboardOptions = KeyboardOptions(keyboardType = KeyboardType.Password)
                            )
                        }
                    }
                    Button(
                        modifier = Modifier.fillMaxWidth(),
                        enabled = enableCreate,
                        onClick = {
                            viewModel.signUp(email, password)
                        },
                        colors = ButtonDefaults.buttonColors(
                            containerColor = accent,
                            contentColor = Color.Black
                        )
                    ) {
                        Text(stringResource(Res.string.create_acc_button))
                    }
                }
            }
        }
    }
}