package data.model

import kotlinx.datetime.LocalDate
import kotlinx.serialization.Serializable

@Serializable
data class Profile(
    val id: String,
    val name: String,
    val dateOfBirth: LocalDate,
    val gender: Gender,
    val description: String
)

@Serializable
data class MyProfile(
    val id: String,
    val name: String,
    val email: String,
    val dateOfBirth: LocalDate,
    val gender: Gender,
    val description: String
)

@Serializable
data class RateInfo(
    val rate: Double,
    val ratingNumber: Long,
    val menNumber: Int,
    val womenNumber: Int
)

@Serializable
data class ProfileWithPhotos(
    val profile: Profile,
    val photos: List<String>
)

@Serializable
data class MyProfileWithRate(
    val profile: MyProfile,
    val photoUrl: String,
    val rateInfo: RateInfo
)

@Serializable
data class MyProfileWithPhotos(
    val profile: MyProfile,
    val photos: List<String>
)

@Serializable
data class GetProfileResponse(
    val item: ProfileWithPhotos
)

@Serializable
data class GetFeedNextResponse(
    val item: ProfileWithPhotos?
)

@Serializable
data class GetMyProfilePageResponse(
    val item: MyProfileWithRate
)

@Serializable
data class GetMyProfileResponse(
    val item: MyProfileWithPhotos
)

@Serializable
enum class Gender {
    MAN,
    WOMAN
}