package presentation.onboarding

import cafe.adriel.voyager.core.model.ScreenModel
import cafe.adriel.voyager.core.model.screenModelScope
import data.model.Gender
import data.repository.ProfileRepository
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.launch

class OnboardingViewModel(
    private val profileRepository: ProfileRepository
) : ScreenModel {

    sealed class State {
        object Init : State()

        object Start : State()

        object SetupDate : State()

        object SetupGender : State()

        object SetupInfo : State()

        object SetupPhotos : State()

        data class Loading(val message: String) : State()

        object SignOut : State()

        object Success : State()
    }

    data class ProfileCreationData(
        var name: String? = null,
        var dateOfBirth: Long? = null,
        var gender: Gender? = null,
        var description: String? = null,
        var photos: List<ByteArray>? = null
    )

    private val profileCreationData = ProfileCreationData()

    private val mutableState: MutableStateFlow<State> = MutableStateFlow(State.Start)

    val state: StateFlow<State> = mutableState.asStateFlow()

    fun startOnboarding() {
        mutableState.value = State.SetupDate
    }

    fun setupDate(dateOfBirth: Long) {
        profileCreationData.dateOfBirth = dateOfBirth
        mutableState.value = State.SetupGender
    }

    fun setupGender(gender: Gender) {
        profileCreationData.gender = gender
        mutableState.value = State.SetupInfo
    }

    fun setupInfo(name: String, description: String?) {
        profileCreationData.name = name
        profileCreationData.description = description
        mutableState.value = State.SetupPhotos
    }

    fun setupPhotos(photos: List<ByteArray>) {
        screenModelScope.launch {
            mutableState.value = State.Loading("Waiting for profile creation...")
            profileRepository.createProfile(
                name = profileCreationData.name!!,
                dateOfBirth = profileCreationData.dateOfBirth!!,
                gender = profileCreationData.gender!!,
                photosData = photos,
                description = profileCreationData.description!!
            )
            mutableState.value = State.Success
        }
    }

    fun setupState(state: State) {
        mutableState.value = state
    }

    fun signOut() {
        screenModelScope.launch {
            mutableState.value = State.Loading("Signing out..")
            profileRepository.signOut()
            mutableState.value = State.SignOut
        }
    }

}