import cafe.adriel.voyager.core.model.ScreenModel
import cafe.adriel.voyager.core.model.screenModelScope
import data.repository.ProfileRepository
import domain.entity.AuthState
import io.github.jan.supabase.exceptions.BadRequestRestException
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.SharingStarted
import kotlinx.coroutines.flow.map
import kotlinx.coroutines.flow.stateIn
import kotlinx.coroutines.launch
import utils.mergeWith

class LoginScreenViewModel(
    private val profileRepository: ProfileRepository
) : ScreenModel {

    sealed class State {
        object Init : State()
        data class Login(val errorMessage: String? = null) : State()
        object Authorized : State()
        object ProfileNotCreated : State()
    }


    private val authState = profileRepository.getAuthState()

    private val mutableState = MutableStateFlow(authState.value.toStateModel())

    val state = authState
        .map { it.toStateModel() }
        .mergeWith(mutableState)
        .stateIn(
            scope = screenModelScope,
            started = SharingStarted.Lazily,
            initialValue = State.Init
        )


    fun signIn(email: String, password: String) {
        screenModelScope.launch {
            try {
                profileRepository.signIn(email, password)
            } catch (e: BadRequestRestException) {
                mutableState.emit(State.Login(e.description))
            }
        }
    }

    private fun AuthState.toStateModel() =
        when(this) {
            is AuthState.NotAuthorized -> State.Login()
            AuthState.Authorized -> State.Authorized
            AuthState.Initial -> State.Init
            AuthState.ProfileRegistration ->State.ProfileNotCreated
        }
}