@file:OptIn(ExperimentalMaterial3Api::class, ExperimentalResourceApi::class)

package utils

import androidx.compose.foundation.Canvas
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.height
import androidx.compose.material3.ExperimentalMaterial3Api
import androidx.compose.material3.SliderState
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.geometry.lerp
import androidx.compose.ui.graphics.Brush
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.StrokeCap
import androidx.compose.ui.graphics.StrokeJoin
import androidx.compose.ui.graphics.drawscope.Stroke
import androidx.compose.ui.text.TextStyle
import androidx.compose.ui.text.drawText
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.text.rememberTextMeasurer
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.LayoutDirection
import androidx.compose.ui.unit.sp
import org.jetbrains.compose.resources.ExperimentalResourceApi
import ui.theme.accent
import ui.theme.montserratFamily
import ui.theme.raterFinish
import ui.theme.raterStart
import ui.theme.sliderInactive
import kotlin.math.roundToInt


@OptIn(ExperimentalMaterial3Api::class)
@Composable
fun CustomTrack(
    sliderState: SliderState,
    height: Dp
) {
    val textMeasurer = rememberTextMeasurer(0)
    val font = montserratFamily()
    Canvas(
        Modifier
            .fillMaxWidth()
            .height(height)
    ) {

        val activeRangeEnd = calcFraction(
            sliderState.valueRange.start,
            sliderState.valueRange.endInclusive,
            sliderState.value.coerceIn(
                sliderState.valueRange.start,
                sliderState.valueRange.endInclusive
            )
        )
        val isRtl = layoutDirection == LayoutDirection.Rtl
        val sliderLeft = Offset(0f, center.y)
        val sliderRight = Offset(size.width, center.y)
        val sliderStart = if (isRtl) sliderRight else sliderLeft
        val sliderEnd = if (isRtl) sliderLeft else sliderRight
        val trackStrokeWidth = height.toPx()
        val gradient = Brush.horizontalGradient(listOf(raterStart, raterFinish))
        drawLine(
            sliderInactive,
            sliderStart,
            sliderEnd,
            trackStrokeWidth,
            StrokeCap.Round
        )
        val sliderValueEnd = Offset(
            sliderStart.x +
                    (sliderEnd.x - sliderStart.x) * activeRangeEnd,
            center.y
        )

        val sliderValueStart = Offset(
            sliderStart.x +
                    (sliderEnd.x - sliderStart.x) * 0f,
            center.y
        )

        drawLine(
            brush = gradient,
            sliderValueStart,
            sliderValueEnd,
            trackStrokeWidth,
            StrokeCap.Round
        )

        for (tick in floatArrayOf(0.0f, 0.25f, 0.5f, 0.75f, 1.0f)) {
            val outsideFraction = tick > activeRangeEnd || tick < 0f
            val text = (((sliderState.steps + 1) * tick).roundToInt() + 1).toString()

            val style = if (outsideFraction) {
                TextStyle(
                    fontSize = 30.sp,
                    fontWeight = FontWeight.ExtraBold,
                    fontFamily = font,
                    color = accent,
                    drawStyle = Stroke(
                        miter = 2f,
                        width = 5f,
                        join = StrokeJoin.Bevel
                    )
                )
            } else {
                TextStyle(
                    fontSize = 30.sp,
                    fontWeight = FontWeight.ExtraBold,
                    fontFamily = font,
                    color = Color.Black,
                )
            }
            val layoutResult = textMeasurer.measure("0", style)
            drawText(
                textMeasurer,
                text = text,
                style = style,
                topLeft = Offset(
                    x = lerp(sliderStart, sliderEnd, tick).x - layoutResult.size.width / 2,
                    y = center.y - layoutResult.size.height / 2
                )
            )
        }
    }
}

fun calcFraction(a: Float, b: Float, pos: Float) =
    (if (b - a == 0f) 0f else (pos - a) / (b - a)).coerceIn(0f, 1f)