package di

import data.network.ProfileApi
import data.network.SupabaseClient
import data.repository.ProfileRepository
import org.koin.dsl.module
import presentation.feed.FeedViewModel
import presentation.profile.MyProfileViewModel


val repositoryModule = module {
    single { ProfileRepository(get(), get()) }
}

val apiModule = module {
    single { ProfileApi(get()) }
    single { SupabaseClient() }
}

val viewModelModule = module {
    factory { FeedViewModel(get()) }
    factory { MyProfileViewModel(get()) }
}

val appModule = module {
    includes(apiModule, repositoryModule, viewModelModule)
}