package presentation.onboarding

import androidx.compose.runtime.Composable
import androidx.compose.runtime.collectAsState
import cafe.adriel.voyager.core.model.rememberScreenModel
import cafe.adriel.voyager.core.screen.Screen
import cafe.adriel.voyager.navigator.LocalNavigator
import cafe.adriel.voyager.navigator.currentOrThrow
import data.repository.ProfileRepository
import navigation.TabNavigation
import org.koin.compose.koinInject
import presentation.main.LoginScreen
import utils.LoadingView


class OnboardingScreen : Screen {

    @Composable
    override fun Content() {

        val repository = koinInject<ProfileRepository>()
        val viewModel = rememberScreenModel { OnboardingViewModel(repository) }
        val state = viewModel.state.collectAsState()
        val navigator = LocalNavigator.currentOrThrow

        when (val currentState = state.value) {
            OnboardingViewModel.State.Init -> {}
            is OnboardingViewModel.State.Loading -> {
                LoadingView(currentState.message)
            }

            OnboardingViewModel.State.Start -> {
                OnboardingStartView(viewModel) {
                    viewModel.signOut()
                    navigator.replaceAll(LoginScreen())
                }
            }

            OnboardingViewModel.State.SetupDate -> {
                SetupDateView(viewModel) {
                    viewModel.setupState(OnboardingViewModel.State.Start)
                }
            }

            OnboardingViewModel.State.SetupGender -> {
                SetupGenderView(viewModel) {
                    viewModel.setupState(OnboardingViewModel.State.SetupDate)
                }
            }

            OnboardingViewModel.State.SetupInfo -> {
                SetupInfoView(viewModel) {
                    viewModel.setupState(OnboardingViewModel.State.SetupGender)
                }
            }

            OnboardingViewModel.State.SetupPhotos -> {
                SetupPhotosView(viewModel) {
                    viewModel.setupState(OnboardingViewModel.State.SetupInfo)
                }
            }

            OnboardingViewModel.State.Success -> {
                TabNavigation()
            }

            OnboardingViewModel.State.SignOut -> {
                navigator.replaceAll(LoginScreen())
            }
        }
    }
}
