@OptIn(ExperimentalMaterial3Api::class, ExperimentalResourceApi::class)
@Composable
fun SetupGenderView(viewModel: OnboardingViewModel, onBackPressed: () -> Unit) {
    var gender by remember { mutableStateOf<Gender?>(null) }

    Scaffold(
        topBar = {
            TopAppBar(
                title = {},
                navigationIcon = {
                    IconButton(
                        onClick = { onBackPressed() }
                    ) {
                        Icon(Icons.AutoMirrored.Filled.ArrowBack, contentDescription = null)
                    }
                },
                colors = TopAppBarDefaults.topAppBarColors(containerColor = Color.Transparent)
            )
        }
    ) { paddingValues ->
        Box(
            modifier = Modifier
                .fillMaxSize()
                .background(color = MaterialTheme.colorScheme.background)
        ) {
            Image(
                modifier = Modifier.matchParentSize(),
                painter = painterResource(Res.drawable.background_blured),
                contentDescription = null,
                contentScale = ContentScale.FillBounds
            )
            Column(
                modifier = Modifier
                    .padding(paddingValues)
                    .padding(start = 16.dp, top = 74.dp, end = 16.dp, bottom = 52.dp),
                verticalArrangement = Arrangement.SpaceBetween
            ) {
                Box(
                    modifier = Modifier.weight(1f)
                ) {
                    Column {
                        Text(
                            text = stringResource(Res.string.onboarding_gender_h1),
                            fontWeight = FontWeight.Bold,
                            fontSize = 34.sp,
                            color = accent
                        )
                        Spacer(modifier = Modifier.height(43.dp))
                        FilterChip(
                            modifier = Modifier
                                .height(56.dp)
                                .fillMaxWidth(),
                            onClick = {
                                gender = Gender.MAN
                            },
                            selected = gender == Gender.MAN,
                            label = {
                                Text(
                                    text = stringResource(Res.string.onboarding_gender_man),
                                    fontSize = 16.sp
                                )
                            },
                            colors = FilterChipDefaults.filterChipColors(
                                containerColor = backgroundButton,
                                selectedContainerColor = accent,
                                selectedLabelColor = Color.Black
                            )
                        )
                        Spacer(modifier = Modifier.height(8.dp))
                        FilterChip(
                            modifier = Modifier
                                .height(56.dp)
                                .fillMaxWidth(),
                            onClick = {
                                gender = Gender.WOMAN
                            },
                            selected = gender == Gender.WOMAN,
                            label = {
                                Text(
                                    text = stringResource(Res.string.onboarding_gender_woman),
                                    fontSize = 16.sp
                                )
                            },
                            colors = FilterChipDefaults.filterChipColors(
                                containerColor = backgroundButton,
                                selectedContainerColor = accent,
                                selectedLabelColor = Color.Black
                            )
                        )
                    }
                }
                Button(
                    enabled = gender != null,
                    modifier = Modifier.fillMaxWidth(),
                    onClick = {
                        viewModel.setupGender(gender!!)
                    },
                    colors = ButtonDefaults.buttonColors(
                        containerColor = accent,
                        contentColor = Color.Black
                    )
                ) {
                    Text(stringResource(Res.string.onboarding_button_next))
                }
            }
        }
    }
}
