data class ProfileDetailScreen(val profileId: String) : Screen {


    @Composable
    override fun Content() {
        val repository = koinInject<ProfileRepository>()

        val viewModel = rememberScreenModel { ProfileDetailViewModel(repository) }
        val screenState = viewModel.state.collectAsState()

        LaunchedEffect(Unit) {
            viewModel.getProfile(profileId)
        }

        when (val currentState = screenState.value) {
            is ProfileDetailViewModel.State.Result -> {
                ProfileView(currentState.profile)
            }

            ProfileDetailViewModel.State.Init -> {}
            ProfileDetailViewModel.State.Loading -> {
                LoadingView()
            }
        }
    }

    @OptIn(ExperimentalMaterial3Api::class)
    @Composable
    private fun ProfileView(
        profileWithPhotos: ProfileWithPhotos
    ) {
        val navigator = LocalNavigator.currentOrThrow
        var currentPhotoIndex by remember { mutableStateOf(0) }
        Scaffold(
            topBar = {
                TopAppBar(
                    title = {
                        Text(
                            "${profileWithPhotos.profile.name}, ${profileWithPhotos.profile.dateOfBirth.toAge()}",
                            fontSize = 30.sp,
                            fontWeight = FontWeight.Bold
                        )
                    },
                    navigationIcon = {
                        IconButton(
                            onClick = { navigator.pop() }
                        ) {
                            Icon(Icons.AutoMirrored.Filled.ArrowBack, contentDescription = null)
                        }
                    },
                    colors = TopAppBarDefaults.topAppBarColors(containerColor = Color.Transparent)
                )
            }
        ) { paddingValues ->
            Box(
                modifier = Modifier.fillMaxSize()
            ) {
                LazyColumn(
                    modifier = Modifier
                        .padding(paddingValues)
                        .padding(bottom = 52.dp)
                ) {
                    item {
                        Box(
                            modifier = Modifier
                                .fillMaxWidth()
                                .height(396.dp)
                        ) {
                            AnimatedContent(
                                targetState = profileWithPhotos.photos
                                    .getOrElse(currentPhotoIndex) { profileWithPhotos.photos.last() },
                                contentAlignment = Alignment.Center
                            ) { targetPhoto ->
                                KamelImage(
                                    modifier = Modifier.matchParentSize(),
                                    resource = asyncPainterResource(targetPhoto),
                                    contentDescription = null,
                                    contentScale = ContentScale.Crop,
                                    onLoading = { progress ->
                                        CircularProgressIndicator(
                                            progress = { progress },
                                            color = accent
                                        )
                                    }
                                )
                            }
                            Row(
                                modifier = Modifier
                                    .padding(16.dp)
                                    .fillMaxWidth(),
                                horizontalArrangement = Arrangement.Center
                            ) {
                                repeat(profileWithPhotos.photos.count()) { index ->
                                    Box(
                                        modifier = Modifier
                                            .padding(2.dp)
                                            .size(8.dp)
                                            .background(
                                                if (index == currentPhotoIndex) accent else Color.Gray,
                                                shape = CircleShape
                                            )
                                    )
                                }
                            }
                            Row {
                                Box(
                                    modifier = Modifier
                                        .weight(0.8f)
                                        .fillMaxSize()
                                        .clickable(
                                            interactionSource = remember { MutableInteractionSource() },
                                            indication = null
                                        ) {
                                            currentPhotoIndex = switchPhotoIndex(
                                                currentPhotoIndex - 1, profileWithPhotos.photos.size
                                            )
                                        }
                                )
                                Box(
                                    modifier = Modifier
                                        .weight(1f)
                                        .fillMaxSize()
                                        .clickable(
                                            interactionSource = remember { MutableInteractionSource() },
                                            indication = null
                                        ) {
                                            navigator.push(ProfileDetailScreen(profileWithPhotos.profile.id))
                                        }
                                )
                                Box(
                                    modifier = Modifier
                                        .weight(0.8f)
                                        .fillMaxSize()
                                        .clickable(
                                            interactionSource = remember { MutableInteractionSource() },
                                            indication = null
                                        ) {
                                            currentPhotoIndex = switchPhotoIndex(
                                                currentPhotoIndex + 1, profileWithPhotos.photos.size
                                            )
                                        }
                                )
                            }
                        }
//                        KamelImage(
//                            modifier = Modifier
//                                .fillMaxWidth()
//                                .height(396.dp),
//                            resource = asyncPainterResource(profileWithPhotos.photos.first()),
//                            contentDescription = null,
//                            contentScale = ContentScale.FillWidth,
//                            onLoading = { progress -> CircularProgressIndicator({ progress }) }
//                        )
                    }
                    item {
                        Spacer(modifier = Modifier.height(8.dp))
                        Box(
                            modifier = Modifier
                                .fillMaxWidth()
                                .padding(horizontal = 16.dp)
                                .clip(RoundedCornerShape(16.dp))
                                .background(color = sliderInactive)
                        ) {
                            Column(
                                modifier = Modifier
                                    .padding(horizontal = 16.dp, vertical = 8.dp)
                            ) {
                                Text(
                                    stringResource(Res.string.onboarding_info_bio),
                                    fontSize = 18.sp,
                                    fontWeight = FontWeight.Bold
                                )
                                HorizontalDivider(modifier = Modifier.padding(vertical = 4.dp))
                                Text(
                                    profileWithPhotos.profile.description
                                )
                            }
                        }
                    }
                }
            }
        }
    }
}
