import utils.PHOTO_COUNT

@OptIn(ExperimentalMaterial3Api::class, ExperimentalResourceApi::class)
@Composable
fun SetupPhotosView(viewModel: OnboardingViewModel, onBackPressed: () -> Unit) {

    val coScope = rememberCoroutineScope()

    val context = LocalPlatformContext.current

    var currentPick = 0
    var showBottomSheet by remember { mutableStateOf(false) }


    var photos by remember { mutableStateOf<List<ByteArray>>(listOf()) }

    val imagePickerLauncher = rememberFilePickerLauncher(
        type = FilePickerFileType.Image,
        selectionMode = FilePickerSelectionMode.Multiple,
        onResult = { files ->
            coScope.launch {
                photos =
                    photos + files.reversed().take(PHOTO_COUNT - photos.size)
                        .map { it.readByteArray(context) }
            }
        }
    )

    val imageReplaceLauncher = rememberFilePickerLauncher(
        type = FilePickerFileType.Image,
        selectionMode = FilePickerSelectionMode.Single,
        onResult = { files ->
            coScope.launch {
                files.firstOrNull()?.let { file ->
                    val tempList = photos.toMutableList()
                    tempList[currentPick] = file.readByteArray(context)
                    photos = tempList.toList()
                }
            }
        }
    )

    Scaffold(
        topBar = {
            TopAppBar(
                title = {},
                navigationIcon = {
                    IconButton(
                        onClick = { onBackPressed() }
                    ) {
                        Icon(Icons.AutoMirrored.Filled.ArrowBack, contentDescription = null)
                    }
                },
                colors = TopAppBarDefaults.topAppBarColors(containerColor = Color.Transparent)
            )
        }
    ) { paddingValues ->
        Box(
            modifier = Modifier
                .fillMaxSize()
                .background(color = MaterialTheme.colorScheme.background)
        )
        {
            Image(
                modifier = Modifier.matchParentSize(),
                painter = painterResource(Res.drawable.background_blured),
                contentDescription = null,
                contentScale = ContentScale.FillBounds
            )
            Column(
                modifier = Modifier
                    .padding(paddingValues)
                    .padding(start = 16.dp, top = 74.dp, end = 16.dp, bottom = 52.dp),
                verticalArrangement = Arrangement.SpaceBetween
            ) {
                Box(
                    modifier = Modifier.weight(1f)
                ) {
                    Column {
                        Text(
                            text = stringResource(Res.string.onboarding_photo_h1),
                            fontWeight = FontWeight.Bold,
                            fontSize = 34.sp,
                            color = accent
                        )
                        Spacer(modifier = Modifier.height(43.dp))
                        LazyVerticalGrid(
                            modifier = Modifier.padding(bottom = 20.dp),
                            columns = GridCells.Adaptive(minSize = 160.dp),
                            verticalArrangement = Arrangement.spacedBy(8.dp),
                            horizontalArrangement = Arrangement.spacedBy(8.dp)
                        ) {
                            items(PHOTO_COUNT) { photoIdx ->
                                Box(
                                    modifier = Modifier
                                        .size(160.dp)
                                        .border(
                                            BorderStroke(
                                                1.dp,
                                                MaterialTheme.colorScheme.onBackground
                                            ),
                                            shape = RoundedCornerShape(12.dp)
                                        ),
                                    contentAlignment = Alignment.Center
                                ) {
                                    val photo = photos.getOrNull(photoIdx)

                                    if (photo == null) {
                                        IconButton(
                                            onClick = {
                                                imagePickerLauncher.launch()
                                            }
                                        ) {
                                            Icon(
                                                Icons.Outlined.AddCircle,
                                                contentDescription = null
                                            )
                                        }
                                    } else {
                                        Image(
                                            modifier = Modifier
                                                .matchParentSize()
                                                .clip(RoundedCornerShape(12.dp))
                                                .clickable {
                                                    currentPick = photoIdx
                                                    showBottomSheet = true
                                                },
                                            painter = BitmapPainter(photo.toImageBitmap()),
                                            contentScale = ContentScale.Crop,
                                            contentDescription = ""
                                        )
                                    }
                                }
                            }
                        }
                    }
                }
                Button(
                    enabled = photos.isNotEmpty(),
                    modifier = Modifier.fillMaxWidth(),
                    onClick = {
                        viewModel.setupPhotos(photos)
                    },
                    colors = ButtonDefaults.buttonColors(
                        containerColor = accent,
                        contentColor = Color.Black
                    )
                ) {
                    Text(stringResource(Res.string.onboarding_button_finish))
                }
            }

            if (showBottomSheet) {
                ModalBottomSheet(
                    onDismissRequest = {
                        showBottomSheet = false
                    }
                ) {
                    Column(
                        modifier = Modifier.padding(bottom = 32.dp, start = 16.dp, end = 16.dp),
                        verticalArrangement = Arrangement.SpaceEvenly
                    ) {
                        OutlinedButton(
                            modifier = Modifier.fillMaxWidth(),
                            onClick = {
                                showBottomSheet = false
                                imageReplaceLauncher.launch()
                            }
                        ) {
                            Text(stringResource(Res.string.onboarding_photo_replace))
                        }
                        OutlinedButton(
                            modifier = Modifier.fillMaxWidth(),
                            onClick = {
                                coScope.launch {
                                    showBottomSheet = false
                                    val tempList = photos.toMutableList()
                                    tempList.removeAt(currentPick)
                                    photos = tempList.toList()
                                }
                            }
                        ) {
                            Text(stringResource(Res.string.onboarding_photo_delete))
                        }
                    }
                }
            }
        }
    }
}
