package presentation.profile

import cafe.adriel.voyager.core.model.ScreenModel
import cafe.adriel.voyager.core.model.screenModelScope
import data.model.MyProfileWithPhotos
import data.repository.ProfileRepository
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.launch

class EditProfileViewModel(
    private val profileRepository: ProfileRepository
) : ScreenModel {

    sealed class State {
        object Init : State()
        data class EditProfile(val profile: MyProfileWithPhotos) : State()
        object Loading : State()
        object Success : State()
        object Error : State()
    }

    private val mutableState: MutableStateFlow<State> = MutableStateFlow(State.Init)

    val state: StateFlow<State> = mutableState.asStateFlow()

    init {
        screenModelScope.launch {
            mutableState.value = State.Loading
            val myProfile = profileRepository.getMyProfile()
            mutableState.value = State.EditProfile(myProfile)
        }
    }

    fun confirmEdit(
        name: String,
        photos: List<EditProfileScreen.ProfilePhoto>,
        description: String
    ) {
        screenModelScope.launch {
            mutableState.emit(State.Loading)
            profileRepository.editProfile(name, photos, description)
            profileRepository.fetchMyProfile()
            mutableState.emit(State.Success)
        }
    }

}