package presentation.profile.detail

import cafe.adriel.voyager.core.model.StateScreenModel
import cafe.adriel.voyager.core.model.screenModelScope
import data.model.ProfileWithPhotos
import data.repository.ProfileRepository
import kotlinx.coroutines.launch

class ProfileDetailViewModel(
    private val repository: ProfileRepository
) : StateScreenModel<ProfileDetailViewModel.State>(State.Init) {

    sealed class State {
        object Init : State()
        object Loading : State()
        data class Result(val profile: ProfileWithPhotos) : State()
    }

    val userId: String
        get() = repository.getUserId()

    fun getProfile(id: String) {
        screenModelScope.launch {
            mutableState.value = State.Loading
            mutableState.value = State.Result(profile = repository.getProfileById(id))
        }
    }
}