@OptIn(ExperimentalMaterial3Api::class, ExperimentalResourceApi::class)
class SettingsScreen : Screen {

    @Composable
    override fun Content() {
        val repository = koinInject<ProfileRepository>()

        val viewModel = rememberScreenModel { SettingsViewModel(repository) }
        val screenState = viewModel.state.collectAsState()

        val navigator = LocalNavigator.currentOrThrow

        when (val currentState = screenState.value) {
            is SettingsViewModel.State.ShowSettings -> {
                SettingsView(viewModel, navigator)
            }

            SettingsViewModel.State.Init -> {}
            SettingsViewModel.State.Loading -> LoadingView(" ...")
            SettingsViewModel.State.Success -> {}
            SettingsViewModel.State.SignOut -> LoadingView("  ...")
            SettingsViewModel.State.SignOutSuccess -> getRootNavigator(navigator).popUntilRoot()
        }
    }

    @Composable
    private fun SettingsView(viewModel: SettingsViewModel, navigator: Navigator) {
        var confirmLogout by remember { mutableStateOf(false) }
        Scaffold(
            topBar = {
                TopAppBar(
                    title = {
                        Text(stringResource(Res.string.my_profile_settings_button))
                    },
                    navigationIcon = {
                        IconButton(
                            onClick = { navigator.pop() }
                        ) {
                            Icon(Icons.AutoMirrored.Filled.ArrowBack, contentDescription = null)
                        }
                    },
                    colors = TopAppBarDefaults.topAppBarColors(containerColor = Color.Transparent)
                )
            }
        ) { paddingValues ->
            Box(
                modifier = Modifier.fillMaxSize()
            ) {
                Column(
                    modifier = Modifier
                        .padding(paddingValues)
                        .padding(start = 16.dp, top = 28.dp, end = 16.dp, bottom = 104.dp)
                ) {
                    Box(modifier = Modifier.weight(1f)) {
                        Column {
                            Text(
                                text = stringResource(Res.string.settings_language),
                                fontSize = 20.sp,
                                fontWeight = FontWeight.Bold
                            )
                            Spacer(modifier = Modifier.height(16.dp))
                            OutlinedTextField(
                                modifier = Modifier.fillMaxWidth(),
                                readOnly = true,
                                value = "",
                                onValueChange = {},
                                trailingIcon = {
                                    IconButton(onClick = {}) {
                                        Icon(Icons.Filled.ArrowDropDown, contentDescription = null)
                                    }
                                }
                            )
                        }
                    }
                    if (confirmLogout) {
                        AlertDialog(
                            onDismissRequest = {
                                confirmLogout = false
                            },
                            title = {
                                Text(stringResource(Res.string.settings_dialog_title))
                            },
                            text = {
                                Text(stringResource(Res.string.settings_dialog_text))
                            },
                            confirmButton = {
                                TextButton(
                                    onClick = {
                                        viewModel.signOut()
                                    }
                                ) {
                                    Text(stringResource(Res.string.settings_sign_out))
                                }
                            },
                            dismissButton = {
                                TextButton(
                                    onClick = {
                                        confirmLogout = false
                                    }
                                ) {
                                    Text(stringResource(Res.string.settings_dialog_cancel))
                                }
                            }
                        )
                    }
                    OutlinedButton(
                        modifier = Modifier.fillMaxWidth(),
                        onClick = {
                            confirmLogout = true
                        }
                    ) {
                        Text(
                            text = stringResource(Res.string.settings_sign_out)
                        )
                    }
                }
            }
        }
    }
}