package data.network

import com.benasher44.uuid.uuid4
import io.github.jan.supabase.createSupabaseClient
import io.github.jan.supabase.gotrue.Auth
import io.github.jan.supabase.gotrue.auth
import io.github.jan.supabase.gotrue.providers.builtin.Email
import io.github.jan.supabase.gotrue.user.UserInfo
import io.github.jan.supabase.storage.Storage
import io.github.jan.supabase.storage.storage

class SupabaseClient {

    private val supabase = createSupabaseClient(
        "https://emvwedokjrmqiehmadti.supabase.co",
        "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImVtdndlZG9ranJtcWllaG1hZHRpIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MDkxMzc4NzMsImV4cCI6MjAyNDcxMzg3M30.mQuVAAIv7V343e8sGGWhbRnTJ2-UHEdiwJ3ulKT3MWg"
    ) {
        install(Auth)
        install(Storage)
    }

    private val auth = supabase.auth
    private val mediaBucket = supabase.storage.from("media")

    fun getAuthState() =
        auth.sessionStatus


    fun getToken(): String? {
        return auth.currentAccessTokenOrNull()
    }

    fun getUser(): UserInfo? {
        return auth.currentUserOrNull()
    }

    suspend fun signUp(email: String, password: String) {
        auth.signUpWith(Email) {
            this.email = email
            this.password = password
        }
    }

    suspend fun signIn(email: String, password: String) {
        auth.signInWith(Email) {
            this.email = email
            this.password = password
        }
    }

    suspend fun signOut() {
        auth.signOut()
    }

    suspend fun uploadFile(file: ByteArray): String {
        val fileName = uuid4().toString() + ".png"
        mediaBucket.upload(fileName, file)
        return mediaBucket.publicUrl(fileName)
    }
}