package presentation.profile

import cafe.adriel.voyager.core.model.ScreenModel
import cafe.adriel.voyager.core.model.screenModelScope
import data.model.MyProfileWithRate
import data.repository.ProfileRepository
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.SharingStarted
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.catch
import kotlinx.coroutines.flow.filterNotNull
import kotlinx.coroutines.flow.map
import kotlinx.coroutines.flow.onStart
import kotlinx.coroutines.flow.stateIn
import kotlinx.coroutines.launch
import utils.mergeWith

class MyProfileViewModel(
    private val profileRepository: ProfileRepository
) : ScreenModel {

    sealed class State {
        object Init : State()
        object Loading : State()
        object SignOut : State()
        object SignOutSuccess : State()
        data class MyProfile(val profileWithRate: MyProfileWithRate) : State()
        object Error: State()
    }

    private val myProfile = profileRepository.getMyProfilePage()

    private val mutableState: MutableStateFlow<State> = MutableStateFlow(State.Init)

    val state: StateFlow<State> = myProfile
        .filterNotNull()
        .map { State.MyProfile(it) as State }
        .catch {
            emit(State.Error)
        }
        .onStart { State.Loading }
        .mergeWith(mutableState)
        .stateIn(
            scope = screenModelScope,
            started = SharingStarted.Lazily,
            initialValue = State.Init
        )

    init {
        fetchProfile()
    }

    fun fetchProfile() {
        screenModelScope.launch {
            profileRepository.fetchMyProfile()
        }
    }
}