class FeedScreen : Screen {

    @Composable
    override fun Content() {
        val repository = koinInject<ProfileRepository>()

        val viewModel = rememberScreenModel(repository.getUserId()) { FeedViewModel(repository) }
        val screenState = viewModel.state.collectAsState()


        when (val currentState = screenState.value) {
            is FeedViewModel.State.RateProfile -> {
                ProfileToRate(currentState.profileWithPhotos, viewModel)
            }

            is FeedViewModel.State.Init -> {}
            is FeedViewModel.State.Loading -> LoadingView()
            is FeedViewModel.State.ProfileWithRate -> {
                ProfileWithRate(
                    profile = currentState.profileWithPhotos,
                    targetRate = currentState.targetRate,
                    viewModel = viewModel
                )
            }

            FeedViewModel.State.NoMoreProfiles -> LoadingView("No more profiles...")
        }
    }

    @Composable
    fun ProfileToRate(
        profile: ProfileWithPhotos,
        viewModel: FeedViewModel
    ) {
        var sliderPosition by remember { mutableFloatStateOf(1f) }

        var rateEnabled by remember { mutableStateOf(true) }
        val interactionSource = remember { MutableInteractionSource() }
        val isPressed by interactionSource.collectIsPressedAsState()
        val scale = animateFloatAsState(if (isPressed) 0.97f else 1f)

        Scaffold(
            topBar = {
                TopAppBar(
                    title = {
                        Text(
                            text = stringResource(Res.string.app_name),
                            fontSize = 16.sp,
                            fontFamily = ultraFontFamily()
                        )
                    },
                    actions = {
                        IconButton(
                            onClick = {}
                        ) {
                            Icon(Icons.Filled.Menu, contentDescription = null)
                        }
                    },
                    colors = TopAppBarDefaults.topAppBarColors(containerColor = Color.Transparent)
                )
            }
        ) { paddingValues ->
            Column(
                modifier = Modifier
                    .padding(paddingValues)
                    .padding(start = 16.dp, top = 21.dp, end = 16.dp, bottom = 104.dp)
            ) {
                ProfileCard(profile)
                Spacer(modifier = Modifier.weight(1f))
                Box(
                    modifier = Modifier
                        .fillMaxWidth()
                        .clip(RoundedCornerShape(36.dp))
                        .background(color = Color.Gray)
                ) {
                    Slider(
                        modifier = Modifier.fillMaxWidth(),
                        value = sliderPosition,
                        onValueChange = { sliderPosition = it },
                        valueRange = 1f..5f,
                        steps = 3,
                        thumb = {
                            Box(
                                modifier = Modifier
                                    .size(54.dp)
                                    .clip(CircleShape)
                                    .background(color = accent),
                                contentAlignment = Alignment.Center
                            ) {
                                Text(
                                    text = sliderPosition.roundToInt().toString(),
                                    fontSize = 30.sp,
                                    fontWeight = FontWeight.ExtraBold,
                                    fontFamily = montserratFamily(),
                                    color = Color.Black
                                )
                            }
                        },
                        track = { sliderState ->
                            CustomTrack(
                                sliderState = sliderState,
                                height = 54.dp
                            )
                        }
                    )
                }
                Spacer(modifier = Modifier.weight(1f))
                Button(
                    enabled = rateEnabled,
                    modifier = Modifier
                        .scale(scale.value)
                        .fillMaxWidth()
                        .height(45.dp),
                    onClick = {
                        viewModel.rateProfile(sliderPosition.roundToInt())
                        rateEnabled = false
                    },
                    interactionSource = interactionSource,
                    colors = ButtonDefaults.buttonColors(
                        containerColor = accent,
                        contentColor = Color.Black
                    )
                ) {
                    Text(stringResource(Res.string.feed_button_rate))
                }
            }
        }
    }

    @Composable
    fun ProfileWithRate(
        profile: ProfileWithPhotos,
        targetRate: Double,
        viewModel: FeedViewModel
    ) {
        val interactionSource = remember { MutableInteractionSource() }
        val isPressed by interactionSource.collectIsPressedAsState()
        val scale = animateFloatAsState(if (isPressed) 0.97f else 1f)

        Scaffold(
            topBar = {
                TopAppBar(
                    title = {
                        Text(
                            text = stringResource(Res.string.app_name),
                            fontSize = 16.sp,
                            fontWeight = FontWeight.Bold
                        )
                    },
                    actions = {
                        IconButton(
                            onClick = {}
                        ) {
                            Icon(Icons.Filled.Menu, contentDescription = null)
                        }
                    },
                    colors = TopAppBarDefaults.topAppBarColors(containerColor = Color.Transparent)
                )
            }
        ) { paddingValues ->
            Column(
                modifier = Modifier
                    .padding(paddingValues)
                    .padding(start = 16.dp, top = 21.dp, end = 16.dp, bottom = 104.dp)
            ) {
                ProfileCard(profile)
                Spacer(modifier = Modifier.weight(1f))
                RatingBox(
                    boxHeight = 54.dp,
                    text = stringResource(Res.string.feed_button_avg_rate, profile.profile.name),
                    circleText = targetRate.toString(),
                    circleWidth = 78.dp,
                    modifier = Modifier.fillMaxWidth()
                )
                Spacer(modifier = Modifier.weight(1f))
                Button(
                    modifier = Modifier
                        .scale(scale.value)
                        .fillMaxWidth()
                        .height(45.dp),
                    onClick = {
                        viewModel.feedNext()
                    },
                    interactionSource = interactionSource,
                    colors = ButtonDefaults.buttonColors(
                        containerColor = accent,
                        contentColor = Color.Black
                    )
                ) {
                    Text(stringResource(Res.string.feed_button_next))
                }
            }
        }
    }

    @Composable
    fun ProfileCard(
        profile: ProfileWithPhotos
    ) {
        val navigator = LocalNavigator.currentOrThrow

        var currentPhotoIndex by remember { mutableStateOf(0) }

        Card(
            modifier = Modifier
                .fillMaxWidth()
                .height(430.dp),
            shape = RoundedCornerShape(20.dp)
        ) {
            Box(
                modifier = Modifier
                    .fillMaxSize()
            ) {
                AnimatedContent(
                    targetState = profile.photos
                        .getOrElse(currentPhotoIndex) { profile.photos.last() },
                    contentAlignment = Alignment.Center
                ) { targetPhoto ->
                    KamelImage(
                        modifier = Modifier.matchParentSize(),
                        resource = asyncPainterResource(targetPhoto),
                        contentDescription = null,
                        contentScale = ContentScale.Crop,
                        onLoading = { progress ->
                            CircularProgressIndicator(
                                progress = { progress },
                                color = accent
                            )
                        }
                    )
                }
                Column(modifier = Modifier.fillMaxHeight()) {
                    Spacer(modifier = Modifier.weight(1f))
                    Box(
                        modifier = Modifier
                            .fillMaxWidth()
                            .height(134.dp)
                            .background(
                                brush = Brush.verticalGradient(
                                    colors = listOf(
                                        Color.Transparent,
                                        Color.Black
                                    )
                                )
                            )
                    )
                }
                Row(
                    modifier = Modifier
                        .padding(16.dp)
                        .fillMaxWidth(),
                    horizontalArrangement = Arrangement.Center
                ) {
                    repeat(profile.photos.count()) { index ->
                        Box(
                            modifier = Modifier
                                .padding(2.dp)
                                .size(8.dp)
                                .background(
                                    if (index == currentPhotoIndex) accent else Color.Gray,
                                    shape = CircleShape
                                )
                        )
                    }
                }

                Row {
                    Box(
                        modifier = Modifier
                            .weight(0.8f)
                            .fillMaxSize()
                            .clickable(
                                interactionSource = remember { MutableInteractionSource() },
                                indication = null
                            ) {
                                currentPhotoIndex = switchPhotoIndex(
                                    currentPhotoIndex - 1, profile.photos.size
                                )
                            }
                    )
                    Box(
                        modifier = Modifier
                            .weight(1f)
                            .fillMaxSize()
                            .clickable(
                                interactionSource = remember { MutableInteractionSource() },
                                indication = null
                            ) {
                                navigator.push(ProfileDetailScreen(profile.profile.id))
                            }
                    )
                    Box(
                        modifier = Modifier
                            .weight(0.8f)
                            .fillMaxSize()
                            .clickable(
                                interactionSource = remember { MutableInteractionSource() },
                                indication = null
                            ) {
                                currentPhotoIndex = switchPhotoIndex(
                                    currentPhotoIndex + 1, profile.photos.size
                                )
                            }
                    )
                }

                Column(
                    verticalArrangement = Arrangement.SpaceBetween
                ) {
                    Spacer(modifier = Modifier.weight(1f))
                    Text(
                        modifier = Modifier
                            .padding(start = 16.dp, bottom = 16.dp),
                        text = "${profile.profile.name}, ${profile.profile.dateOfBirth.toAge()}",
                        fontSize = 30.sp,
                        fontWeight = FontWeight.Bold,
                        color = whiteOn
                    )
                }
            }

        }
    }
}
