import utils.toAge


@OptIn(ExperimentalResourceApi::class)
class MyProfileScreen : Screen {

    @Composable
    override fun Content() {

        val repository = koinInject<ProfileRepository>()


        val viewModel =
            rememberScreenModel(repository.getUserId()) { MyProfileViewModel(repository) }
        val screenState by viewModel.state.collectAsState()

        val navigator = LocalNavigator.currentOrThrow

        when (val currentState = screenState) {
            is MyProfileViewModel.State.MyProfile -> {
                MyProfileView(currentState.profileWithRate, viewModel)
            }

            MyProfileViewModel.State.Init -> {}
            MyProfileViewModel.State.Loading -> {
                LoadingView()
            }

            MyProfileViewModel.State.Error -> ErrorView()
            MyProfileViewModel.State.SignOut -> LoadingView("Signing out...")
            MyProfileViewModel.State.SignOutSuccess -> getRootNavigator(navigator).popUntilRoot()
        }
    }

    @Composable
    private fun MyProfileView(
        profileWithRate: MyProfileWithRate,
        viewModel: MyProfileViewModel
    ) {
        val navigator = LocalNavigator.currentOrThrow

        Box(
            modifier = Modifier
                .fillMaxSize()
        ) {
            Column(
                modifier = Modifier
                    .padding(start = 16.dp, top = 26.dp, end = 16.dp, bottom = 104.dp),
                verticalArrangement = Arrangement.SpaceBetween,
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Box(modifier = Modifier.weight(1f)) {
                    Column(
                        horizontalAlignment = Alignment.CenterHorizontally
                    ) {
                        KamelImage(
                            modifier = Modifier
                                .size(121.dp)
                                .clip(RoundedCornerShape(8.dp))
                                .clickable {
                                    navigator.push(ProfileDetailScreen(profileWithRate.profile.id))
                                },
                            resource = asyncPainterResource(profileWithRate.photoUrl),
                            contentDescription = null,
                            contentScale = ContentScale.Crop,
                            onLoading = { progress -> CircularProgressIndicator({ progress }) }
                        )
                        Spacer(modifier = Modifier.height(8.dp))
                        Text(
                            text = "${profileWithRate.profile.name}, ${profileWithRate.profile.dateOfBirth.toAge()}",
                            fontSize = 28.sp,
                            fontWeight = FontWeight.Bold,
                            color = Color.White
                        )
                        Spacer(modifier = Modifier.height(4.dp))
                        Text(
                            text = profileWithRate.profile.email,
                            fontSize = 12.sp,
                            color = primary100
                        )
                        Spacer(modifier = Modifier.height(36.dp))
                        RatingBox(
                            boxHeight = 54.dp,
                            text = stringResource(Res.string.my_profile_my_rating),
                            circleText = profileWithRate.rateInfo.rate.toString(),
                            circleWidth = 69.dp,
                            modifier = Modifier.fillMaxWidth()
                        )
                        Spacer(modifier = Modifier.height(8.dp))
                        RatingBox(
                            boxHeight = 54.dp,
                            text = stringResource(Res.string.my_profile_rate_count_title),
                            circleText = stringResource(
                                Res.string.my_profile_rate_count,
                                profileWithRate.rateInfo.ratingNumber
                            ),
                            circleWidth = 145.dp,
                            modifier = Modifier.fillMaxWidth()
                        )
                        Spacer(modifier = Modifier.height(8.dp))
                        Row(
                            horizontalArrangement = Arrangement.SpaceEvenly
                        ) {
                            RatingBox(
                                boxHeight = 54.dp,
                                text = stringResource(Res.string.my_profile_man),
                                textSize = 13.sp,
                                circleText = profileWithRate.rateInfo.menNumber.toString(),
                                circleWidth = 84.dp,
                                modifier = Modifier.weight(1f)
                            )
                            Spacer(modifier = Modifier.width(8.dp))
                            RatingBox(
                                boxHeight = 54.dp,
                                text = stringResource(Res.string.my_profile_woman),
                                textSize = 13.sp,
                                circleText = profileWithRate.rateInfo.womenNumber.toString(),
                                circleWidth = 84.dp,
                                modifier = Modifier.weight(1f)
                            )
                        }
                    }

                }
                OutlinedButton(
                    modifier = Modifier.fillMaxWidth(),
                    onClick = {
                        navigator.push(EditProfileScreen())
                    }
                ) {
                    Text(
                        text = stringResource(Res.string.my_profile_edit_profile_button)
                    )
                }
                OutlinedButton(
                    modifier = Modifier.fillMaxWidth(),
                    onClick = {
                        navigator.push(SettingsScreen())
                    }
                ) {
                    Text(
                        text = stringResource(Res.string.my_profile_settings_button)
                    )
                }
            }
        }
    }
}
