package utils

import androidx.compose.runtime.Composable
import client.composeapp.generated.resources.Res
import client.composeapp.generated.resources.onboarding_gender_man
import client.composeapp.generated.resources.onboarding_gender_woman
import data.model.Gender
import kotlinx.datetime.Clock
import kotlinx.datetime.LocalDate
import kotlinx.datetime.TimeZone
import kotlinx.datetime.toLocalDateTime
import kotlinx.datetime.yearsUntil
import org.jetbrains.compose.resources.stringResource

fun LocalDate.toAge() =
    this.yearsUntil(Clock.System.now().toLocalDateTime(TimeZone.UTC).date)

@Composable
fun genderToString(gender: Gender): String {
    return when (gender) {
        Gender.MAN -> stringResource(Res.string.onboarding_gender_man)
        Gender.WOMAN -> stringResource(Res.string.onboarding_gender_woman)
    }
}

fun switchPhotoIndex(newIdx: Int, size: Int) =
    when {
        newIdx < 0 -> 0
        newIdx >= size -> size - 1
        else -> newIdx
    }