class EditProfileScreen : Screen {

    sealed class ProfilePhoto {
        data class PhotoUrl(val url: String) : ProfilePhoto()
        data class PhotoByteArray(val byteArray: ByteArray) : ProfilePhoto()
    }

    @Composable
    override fun Content() {
        val repository = koinInject<ProfileRepository>()
        val viewModel = rememberScreenModel { EditProfileViewModel(repository) }
        val state = viewModel.state.collectAsState()
        val navigator = LocalNavigator.currentOrThrow

        when (val currentState = state.value) {
            is EditProfileViewModel.State.EditProfile -> {
                EditProfileView(currentState.profile, viewModel)
            }

            EditProfileViewModel.State.Init -> {}
            EditProfileViewModel.State.Loading -> {
                LoadingView()
            }

            EditProfileViewModel.State.Error -> {}
            EditProfileViewModel.State.Success -> {
                navigator.pop()
            }
        }
    }

    @OptIn(ExperimentalMaterial3Api::class)
    @Composable
    private fun EditProfileView(
        profileWithPhotos: MyProfileWithPhotos,
        viewModel: EditProfileViewModel
    ) {

        val coScope = rememberCoroutineScope()
        val navigator = LocalNavigator.currentOrThrow
        val context = LocalPlatformContext.current

        var name by remember { mutableStateOf(profileWithPhotos.profile.name) }
        var description by remember { mutableStateOf(profileWithPhotos.profile.description) }

        var showBottomSheet by remember { mutableStateOf(false) }
        var photos by remember {
            mutableStateOf<List<ProfilePhoto>>(profileWithPhotos.photos.map {
                ProfilePhoto.PhotoUrl(
                    it
                )
            })
        }

        var currentPick = 0

        val isConfirm = (name.isNotBlank() && name != profileWithPhotos.profile.name) ||
                description != profileWithPhotos.profile.description ||
                (photos.isNotEmpty() &&
                        (photos.count() != profileWithPhotos.photos.count() ||
                                photos.any { it is ProfilePhoto.PhotoByteArray }))

        val imagePickerLauncher = rememberFilePickerLauncher(
            type = FilePickerFileType.Image,
            selectionMode = FilePickerSelectionMode.Multiple,
            onResult = { files ->
                coScope.launch {
                    photos =
                        photos + files.reversed().take(PHOTO_COUNT - photos.size)
                            .map { ProfilePhoto.PhotoByteArray(it.readByteArray(context)) }
                }
            }
        )

        val imageReplaceLauncher = rememberFilePickerLauncher(
            type = FilePickerFileType.Image,
            selectionMode = FilePickerSelectionMode.Single,
            onResult = { files ->
                coScope.launch {
                    files.firstOrNull()?.let { file ->
                        val tempList = photos.toMutableList()
                        tempList[currentPick] =
                            ProfilePhoto.PhotoByteArray(file.readByteArray(context))
                        photos = tempList.toList()
                    }
                }
            }
        )

        Scaffold(
            topBar = {
                TopAppBar(
                    title = {
                        Text(stringResource(Res.string.my_profile_edit_profile_button))
                    },
                    navigationIcon = {
                        IconButton(
                            onClick = { navigator.pop() }
                        ) {
                            Icon(Icons.AutoMirrored.Filled.ArrowBack, contentDescription = null)
                        }
                    },
                    colors = TopAppBarDefaults.topAppBarColors(containerColor = Color.Transparent)
                )
            }
        ) { paddingValues ->
            Column(
                modifier = Modifier
                    .fillMaxSize()
                    .padding(paddingValues)
                    .padding(start = 16.dp, top = 28.dp, end = 16.dp, bottom = 104.dp)
            ) {
                Box(modifier = Modifier.weight(1f)) {
                    LazyColumn {
                        item {
                            Column {
                                Text(
                                    text = stringResource(Res.string.edit_profile_photo),
                                    fontSize = 20.sp,
                                    fontWeight = FontWeight.Bold
                                )
                                Spacer(modifier = Modifier.height(8.dp))
                                LazyHorizontalGrid(
                                    modifier = Modifier
                                        .height(180.dp)
                                        .fillMaxWidth()
                                        .padding(bottom = 20.dp),
                                    rows = GridCells.Adaptive(minSize = 160.dp),
                                    verticalArrangement = Arrangement.spacedBy(8.dp),
                                    horizontalArrangement = Arrangement.spacedBy(8.dp)
                                ) {
                                    items(PHOTO_COUNT) { photoIdx ->
                                        Box(
                                            modifier = Modifier
                                                .size(160.dp)
                                                .border(
                                                    BorderStroke(
                                                        1.dp,
                                                        MaterialTheme.colorScheme.onBackground
                                                    ),
                                                    shape = RoundedCornerShape(12.dp)
                                                ),
                                            contentAlignment = Alignment.Center
                                        ) {
                                            val photo = photos.getOrNull(photoIdx)

                                            when (photo) {
                                                null -> {
                                                    IconButton(
                                                        onClick = {
                                                            imagePickerLauncher.launch()
                                                        }
                                                    ) {
                                                        Icon(
                                                            Icons.Outlined.AddCircle,
                                                            contentDescription = null
                                                        )
                                                    }
                                                }

                                                is ProfilePhoto.PhotoByteArray -> {
                                                    Image(
                                                        modifier = Modifier
                                                            .matchParentSize()
                                                            .clip(RoundedCornerShape(12.dp))
                                                            .clickable {
                                                                currentPick = photoIdx
                                                                showBottomSheet = true
                                                            },
                                                        painter = BitmapPainter(
                                                            photo.byteArray.toImageBitmap()
                                                        ),
                                                        contentScale = ContentScale.Crop,
                                                        contentDescription = ""
                                                    )
                                                }

                                                is ProfilePhoto.PhotoUrl -> {
                                                    KamelImage(
                                                        modifier = Modifier
                                                            .matchParentSize()
                                                            .clip(RoundedCornerShape(12.dp))
                                                            .clickable {
                                                                currentPick = photoIdx
                                                                showBottomSheet = true
                                                            },
                                                        resource = asyncPainterResource(photo.url),
                                                        contentDescription = null,
                                                        contentScale = ContentScale.Crop,
                                                        onLoading = { progress ->
                                                            CircularProgressIndicator(
                                                                progress = { progress },
                                                                color = accent
                                                            )
                                                        }
                                                    )
                                                }
                                            }
                                        }
                                    }
                                }
                            }

                        }
                        item {
                            Column {
                                Text(
                                    text = stringResource(Res.string.edit_profile_info),
                                    fontSize = 20.sp,
                                    fontWeight = FontWeight.Bold
                                )
                                Spacer(modifier = Modifier.height(8.dp))
                                OutlinedTextField(
                                    modifier = Modifier.fillMaxWidth(),
                                    value = name,
                                    onValueChange = {
                                        if (it.length <= 32) name = it
                                    },
                                    singleLine = true,
                                    label = { Text(stringResource(Res.string.onboarding_info_name)) }
                                )
                                Spacer(modifier = Modifier.height(8.dp))
                                OutlinedTextField(
                                    modifier = Modifier
                                        .height(140.dp)
                                        .fillMaxWidth(),
                                    value = description,
                                    onValueChange = { if (it.length <= 500) description = it },
                                    label = { Text(stringResource(Res.string.onboarding_info_bio)) }
                                )
                            }
                        }
                    }
                }
                if (showBottomSheet) {
                    ModalBottomSheet(
                        onDismissRequest = {
                            showBottomSheet = false
                        }
                    ) {
                        Column(
                            modifier = Modifier.padding(bottom = 32.dp, start = 16.dp, end = 16.dp),
                            verticalArrangement = Arrangement.SpaceEvenly
                        ) {
                            OutlinedButton(
                                modifier = Modifier.fillMaxWidth(),
                                onClick = {
                                    showBottomSheet = false
                                    imageReplaceLauncher.launch()
                                }
                            ) {
                                Text(stringResource(Res.string.onboarding_photo_replace))
                            }
                            OutlinedButton(
                                modifier = Modifier.fillMaxWidth(),
                                onClick = {
                                    coScope.launch {
                                        showBottomSheet = false
                                        val tempList = photos.toMutableList()
                                        tempList.removeAt(currentPick)
                                        photos = tempList.toList()
                                    }
                                }
                            ) {
                                Text(stringResource(Res.string.onboarding_photo_delete))
                            }
                        }
                    }
                }
                Spacer(modifier = Modifier.height(16.dp))
                Button(
                    modifier = Modifier.fillMaxWidth(),
                    enabled = isConfirm,
                    onClick = {
                        viewModel.confirmEdit(name, photos, description)
                    },
                    colors = ButtonDefaults.buttonColors(
                        containerColor = accent,
                        contentColor = Color.Black
                    )
                ) {
                    Text(stringResource(Res.string.edit_profile_save))
                }
            }
        }
    }
}