package data.repository

import data.model.CreateProfileRequest
import data.model.EditProfileRequest
import data.model.Gender
import data.model.MyProfileWithPhotos
import data.model.MyProfileWithRate
import data.model.ProfileWithPhotos
import data.network.ProfileApi
import data.network.SupabaseClient
import domain.entity.AuthState
import io.github.jan.supabase.gotrue.SessionStatus
import io.github.jan.supabase.gotrue.user.UserInfo
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.Dispatchers
import kotlinx.coroutines.flow.MutableSharedFlow
import kotlinx.coroutines.flow.SharingStarted
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.flow
import kotlinx.coroutines.flow.stateIn
import kotlinx.datetime.Instant
import kotlinx.datetime.TimeZone
import kotlinx.datetime.toLocalDateTime
import presentation.profile.EditProfileScreen

class ProfileRepository(
    private val profileApi: ProfileApi,
    private val supabaseClient: SupabaseClient
) {

    private val coroutineScope = CoroutineScope(Dispatchers.Default)

    private val updateMyProfileEvents = MutableSharedFlow<Unit>(replay = 1)

    private val authState = flow {
        supabaseClient.getAuthState()
            .collect {
                if (it is SessionStatus.NotAuthenticated) {
                    emit(AuthState.NotAuthorized)
                } else {
                    val user = supabaseClient.getUser()
                    if (user == null) {
                        emit(AuthState.NotAuthorized)
                    } else {
                        if (profileApi.profileIsCreated().isCreated) {
                            emit(AuthState.Authorized)
                        } else {
                            emit(AuthState.ProfileRegistration)
                        }
                    }

                }
            }
    }.stateIn(
        scope = coroutineScope,
        started = SharingStarted.Lazily,
        initialValue = AuthState.Initial
    )

    private val myProfileData: StateFlow<MyProfileWithRate?> = flow {
        updateMyProfileEvents.emit(Unit)
        updateMyProfileEvents.collect {
            val response = profileApi.getMyProfilePage()
            emit(response.item)
        }
    }.stateIn(
        scope = coroutineScope,
        started = SharingStarted.Lazily,
        initialValue = null
    )

    fun getAuthState(): StateFlow<AuthState> {
        return authState
    }

    fun getUserId(): String {
        return supabaseClient.getUser()?.id ?: throw IllegalStateException("User id is null")
    }

    fun getUser(): UserInfo {
        return supabaseClient.getUser() ?: throw IllegalStateException("User does not exist")
    }

    suspend fun signUp(email: String, password: String) {
        supabaseClient.signUp(email, password)
    }

    suspend fun signIn(email: String, password: String) {
        supabaseClient.signIn(email, password)
    }

    suspend fun signOut() {
        supabaseClient.signOut()
    }

    suspend fun createProfile(
        name: String,
        dateOfBirth: Long,
        gender: Gender,
        photosData: List<ByteArray>,
        description: String
    ): String {
        val userInfo = getUser()
        val photosUrlList = photosData.map { data ->
            supabaseClient.uploadFile(data)
        }

        return profileApi.createProfile(
            CreateProfileRequest(
                id = userInfo.id,
                name = name,
                email = userInfo.email ?: "",
                dateOfBirth = Instant.fromEpochMilliseconds(dateOfBirth)
                    .toLocalDateTime(TimeZone.UTC).date,
                gender = gender,
                photosUrlList = photosUrlList,
                description = description
            )
        ).id
    }

    suspend fun editProfile(
        name: String,
        photosData: List<EditProfileScreen.ProfilePhoto>,
        description: String
    ) {
        val photosUrlList = photosData.map { photo ->
            if (photo is EditProfileScreen.ProfilePhoto.PhotoByteArray) {
                supabaseClient.uploadFile(photo.byteArray)
            } else (photo as EditProfileScreen.ProfilePhoto.PhotoUrl).url
        }
        profileApi.editProfile(
            EditProfileRequest(
                name = name,
                photosUrlList = photosUrlList,
                description = description
            )
        )
    }

    suspend fun getProfileById(id: String): ProfileWithPhotos {
        return profileApi.getProfile(id).item
    }

    fun getMyProfilePage(): StateFlow<MyProfileWithRate?> {
        return myProfileData
    }

    suspend fun getMyProfile(): MyProfileWithPhotos {
        return profileApi.getMyProfile().item
    }

    suspend fun fetchMyProfile() {
        updateMyProfileEvents.emit(Unit)
    }

    suspend fun feedNext(): ProfileWithPhotos? {
        return profileApi.getFeedNext().item
    }

    suspend fun rateProfile(targetProfileId: String, score: Int): Double {
        return profileApi.rateProfile(targetProfileId, score).profileRating
    }
}