import ui.theme.accent
import ui.theme.primary100

@OptIn(ExperimentalResourceApi::class)
class WelcomeScreen : Screen {

    @Composable
    override fun Content() {
        val navigator = LocalNavigator.currentOrThrow

        Box(
            modifier = Modifier
                .fillMaxSize()
                .background(color = MaterialTheme.colorScheme.background)
        ) {
            Column(
                modifier = Modifier
                    .padding(start = 16.dp, top = 100.dp, end = 16.dp, bottom = 52.dp),
                verticalArrangement = Arrangement.SpaceBetween
            ) {
                Box(
                    modifier = Modifier.weight(1f)
                ) {
                    Column {
                        Text(
                            text = stringResource(Res.string.welcome_h1),
                            fontWeight = FontWeight.Bold,
                            fontSize = 34.sp,
                            color = primary100
                        )
                        Spacer(modifier = Modifier.height(8.dp))
                        Text(
                            text = stringResource(Res.string.welcome_h2),
                            fontSize = 22.sp,
                            color = primary100
                        )
                        Spacer(modifier = Modifier.height(39.dp))
                        Image(
                            modifier = Modifier
                                .size(width = 310.dp, height = 276.dp)
                                .align(Alignment.CenterHorizontally),
                            painter = painterResource(Res.drawable.pomarkchenok),
                            contentDescription = null
                        )
                    }
                }
                Button(
                    modifier = Modifier.fillMaxWidth(),
                    onClick = {
                        navigator.replaceAll(LoginScreen())
                    },
                    colors = ButtonDefaults.buttonColors(
                        containerColor = accent,
                        contentColor = Color.Black
                    )
                ) {
                    Text(stringResource(Res.string.welcome_button))
                }
            }
        }
    }


}